@use 'variables' as v;

@mixin contenedor {
    width: min(90%, 120rem);
    margin: 0 auto;
}

@mixin contenedor-middle {
    width: min(80%, 90rem);
    margin: 0 auto;
}

@mixin enlace($color, $fuente: v.$texto) {
    all: unset;
    color: $color;
    font-size: 1.6rem;
    text-transform: uppercase;
    font-family: $fuente;
    position: relative;
    height: min-content;
    cursor: pointer;

    &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: -5px;
        width: calc(100% + 10px);
        height: 1px;
        background-color: v.$white;
        z-index: 1;
        transform: scaleX(0);
        transform-origin: center;
        transition: transform 300ms ease-in-out;
    }
    &:hover::before{
        transform: scaleX(1);
    }
}

@mixin resetLista {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin bg-with-overlay ($nombreImagen, $overlayColor, $opacidad, $bloque) {
    background-image: image-set(url('/build/img/design/#{$nombreImagen}.avif') 1x, url('/build/img/design/#{$nombreImagen}.webp') 1x, url('/build/img/design/#{$nombreImagen}.jpg') 1x);
    background-size: cover;
    background-position: center;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $overlayColor;
        opacity: $opacidad;
    }

    #{$bloque}__contenido {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 100%;
    
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}


/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin xl_desktop {
    @media (min-width: v.$xl_desktop) {
        @content;
    }
}