@use 'base/variables' as v;
@use 'base/mixins' as m;

// Estilos generales para el texto
body {
    font-family: v.$texto;
    font-weight: v.$light;
    font-size: 1.6rem;
    line-height: 1.5;

    @include m.tablet {
        font-size: 1.8rem;
    }

    @include m.desktop {
        font-size: 2rem;
    }
}

h1, h2 {
    color: v.$purple_700;
    border-bottom: 2px solid v.$purple_200;
}

h1, h2, h3 {
    font-family: v.$titulo;
    font-weight: v.$medium;
    margin: 0 0 1.5rem 0;
    text-align: center;
}

em {
    color: v.$purple_300;
}

// Tamaño de letra de los headings
h1 {
    font-size: 5rem;
    
    @include m.tablet {
        font-size: 5.5rem;
    }

    @include m.desktop {
        font-size: 7rem;
    }

    @include m.xl-desktop {
        font-size: 9rem;
    }
}

h2 {
    font-size: 3.5rem;
    
    @include m.tablet {
        font-size: 4.5rem;
    }

    @include m.desktop {
        font-size: 7rem;
    }
}

h3 {
    font-size: 2.8rem;
    margin-bottom: 0;

    @include m.tablet {
        font-size: 3rem;
    }

    @include m.desktop {
        font-size: 4rem;
    }
}