@use 'base/variables' as v;

.preguntas-frecuentes {
    &__titulo {
        margin: 0 auto;
        width: 80%;
    }
}

.accordion {
    margin-top: 2rem;
    $modificador: &;
    
    &__item {
        display: flex;
        flex-direction: column;
        justify-content: center;

        // Seleccionar .accordion__fondo-titulo a partir del primero
        &:nth-child(n+2){
            #{$modificador}__fondo-titulo{
                border-top: none;
            }
        }
    }

    &__fondo-titulo {
        background-color: v.$purple_300;
        border: 3px solid v.$purple_400;
        transition: background-color 200ms;

        &:hover {
            background-color: lighten(v.$purple_300, 8);
        }
    }

    &__contenedor-titulo {
        display: flex;
        align-items: center;
        gap: 5rem;

        padding: 1rem 0;
        min-height: 6rem;
    
        &:hover {
            cursor: pointer;
        }
    }

    &__titulo,
    &__icono {
        color: v.$white;
    }

    &__titulo {
        flex: 98%;
        text-align: left;
    }

    &__icono {
        flex: 2%;
        transform: rotate(180deg);
        transition: transform 250ms ease-in;
    }

    &__contenido {
        max-height: 0;

        padding: 0;
        margin: 0;
        
        overflow: hidden;
        transition: max-height 400ms, margin 400ms, padding 400ms, box-shadow 400ms;
    }
}

// Estilos para cuando la clase activo está presente en el código
.activo {
    .accordion__fondo-titulo {
        background-color: lighten(v.$purple_300, 8);
    }

    .accordion__contenido {
        margin: 1.5rem 0;
        padding: 0 1rem;
    }

    .accordion__contenedor-contenido {
        z-index: -1;
        -webkit-box-shadow: -36px 0px 50px -5px rgba(252, 162, 231, 0.81), 36px 0px 50px -5px rgba(252, 162, 231, 0.81);
        -moz-box-shadow: -36px 0px 50px -5px rgba(252, 162, 231, 0.81), 36px 0px 50px -5px rgba(252, 162, 231, 0.81);
        box-shadow: -36px 0px 50px -5px rgba(252, 162, 231, 0.81), 36px 0px 50px -5px rgba(252, 162, 231, 0.81);
    }
}

// Esta clase se agrega al item inactivo siguiente al que está activo para agregarle el border-top solo en este momento
.inactivo {
    .accordion__fondo-titulo {
        border-top: 3px solid v.$purple_400 !important;
    }
}

