@use 'base/mixins' as m;

// Estilos globales
html {
    font-size: 62.5%; /* 1rem = 10px */
    box-sizing: border-box;
    height: 100%;
    scroll-behavior: smooth;
}
*, *:before, *:after {
    box-sizing: inherit;
}

html, 
body {
    position: relative;
    height: 100%;
}

section[id] {
    scroll-margin-top: 6.5rem;

    @include m.tablet {
        scroll-margin-top: 10.75rem;
    }
}

a {
    text-decoration: none;
}

img {
    width: 100%;
    height: auto;
}

ul {
    @include m.resetLista;
}

// Utilidades
.contenedor {
    @include m.contenedor-middle;

    @include m.tablet {
        @include m.contenedor;
    }
}

.mtop-3 {
    margin-top: 3rem;
}

.ptop-3 {
    padding-top: 3rem;
}
