@use 'base/mixins' as m;
@use 'base/variables' as v;

header {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;

    display: flex;
    justify-content: space-between;

    padding: 0.75rem 2rem;
    width: 100%;
    height: 6.5rem;

    background-color: v.$purple_400;

    @include m.tablet {
        flex-direction: column;

        padding: 0.75rem 0 0 0;
        height: 10.75rem;

        .contenedor-logo {
            display: flex;
            justify-content: center;
        }
    }

    @include m.desktop {
        flex-direction: row;

        height: 6.5rem;
        padding: 0.75rem 5rem;
    }
}

.logo {
    height: 5rem;
}

.boton-menu {
    z-index: 2;

    @include m.tablet {
        display: none;
    }

    &__icon{
        position: absolute;
        right: 2rem;
        bottom: 2rem;

        color: #ffffff;
        font-size: 2.5rem;
    }
}

.menu {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 3.5rem;

    background-color: rgb(78 1 43 / 0.9);
    padding: 7rem 2rem;
    height: 100vh;
    width: 100%;

    opacity: 0;
    visibility: hidden;
    transition: opacity 150ms ease-in, visibility 150ms ease-in;

    @include m.tablet {
        position: relative;

        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 3rem;
        
        background-color: v.$purple_500;
        height: 100%;
        padding: 0.75rem 0;

        opacity: 1;
        visibility: visible;
    }

    @include m.desktop {
        background-color: initial;
        width: min-content;
    }

    &__enlace {
        @include m.enlace(v.$white);
        font-size: 1.4rem;
    }
}

.mobile-menu {
    opacity: 1;
    visibility: visible;
}

