@use 'base/mixins' as m;
@use 'base/variables' as v;

.hero {
    @include m.bg-with-overlay("hero", v.$black, 0.65, ".hero");

    padding: 2rem;
    height: 35rem;

    // Se aplica margin debido a que el header es esta en position:fixed y el valor del margin es igual a la altura que tiene el header
    margin-top: 6.5rem;

    @include m.tablet {
        margin-top: 10.75rem;
    }

    @include m.desktop {
        margin-top: 6.5rem;
        height: 45rem;
    }

    @include m.xl-desktop {
        height: 50rem;
    }

    &__contenido {
        @include m.telefono {
            align-items: flex-start !important;
        }
    }

    &__titulo {
        margin: 0;
        border: none;

        text-transform: uppercase;
        color: v.$purple_200;
    }

    &__msg{
        font-size: 1.5rem;
        font-weight: v.$extraLight;
        color: v.$white;

        span {
            display: block;
            margin-bottom: 0.6rem;    

            font-size: 2.2rem;
            font-weight: v.$medium;
        }

        @include m.desktop {
            font-size: 1.8rem;

            span {
                font-size: 2.8rem;
            }
        }

        @include m.xl-desktop {
            font-size: 2rem;

            span {
                font-size: 3.5rem;
            }
        }
    }

    &__ubicacion {
        font-size: 1.6rem;
        color: v.$purple_100;

        i {
            margin-right: 1rem;
        }
    }

    &__msg,
    &__ubicacion {
        text-align: center;
        
        @include m.telefono {
            text-align: start;
        }
    }
}