@use 'base/variables' as v;
@use 'base/mixins' as m;

.swiper-instalaciones { 
    height: 20rem;
    margin-top: 3rem;

    border-radius: 1.5rem;

    @include m.telefono {
        height: 30rem;
    }

    @include m.tablet {
        height: 40rem;
    }

    @include m.desktop {
        height: 50rem;
    }

    &__slide {
        img {
            height: 100%;  
        }
    }

    &__pagination {
        background-color: rgba(252,162,231,0.3);
        border-radius: 1.5rem;

        // Se usa !important para sobreescribir los estilos de Swiper JS
        width: 100% !important;
        
        left: 50% !important;
        transform: translateX(-50%) !important;

        @include m.telefono {
            width: 80% !important;
        }

        @include m.tablet {
            width: 50% !important;
        }

        & .swiper-pagination-bullet-active {
            background: v.$purple_300;
        }
    }

    &__button-prev,
    &__button-next {
        display: none !important;
        color: v.$white !important;

        @include m.tablet {
            display: flex !important;
        }
    }
}

  
