@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros {
    &__imagen {
        display: none;   
    }

    @include m.desktop {
        &__grid {
            display: grid;
            grid-template-columns: 40% 60%;
            align-items: center;
        }

        &__imagen {
            display: block;

            max-width: 100%;
            height: auto;

            padding: 2rem;
            margin: 0 auto;
        }

        &__contenido {
            grid-column: 2 / 3;
            padding: 2rem 2rem 2rem 4rem;
        }

        &__titulo {
            text-transform: uppercase;
            font-weight: v.$bold;
        }

        &__texto {
            text-align: center;
        }
    }

    @include m.xl_desktop {
        &__imagen {
            max-width: 90%;
        }
    }
}