@use 'base/variables' as v;
@use 'base/mixins' as m;

footer {
    background-color: v.$purple_300;
    margin-top: 3rem;

    @include m.tablet {
        margin-top: 0;
    }
}

.copyright {
    padding: 1rem 0;

    @include m.tablet {
        display: flex;
        justify-content: center;
    }

    p {
        margin: 0;
        color: v.$white;
        text-align: center;
        font-size: 1.4rem;
    }
}

.guion-derechos-reservados {
    display: none;

    @include m.tablet {
        display: inline-block;
    }
}