@use 'base/variables' as v;
@use 'base/mixins' as m;

.fondo-requisitos,
.fondo-requisitos::after {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.fondo-requisitos {
    background-image: url("/build/img/design/fondo-requisitos-1.svg");
    position: relative;

    &::after {
        content: '';
        
        position: absolute;
        bottom: -18rem;
        z-index: -1;

        background-image: url("/build/img/design/fondo-requisitos-2.svg");

        height: 18rem;
        width: 100%;
    }

    @include m.telefono {
        background-image: url("/build/img/design/fondo-requisitos-1-telefono.svg");

        &::after {
            height: 25rem;
        }
    }

    @include m.tablet {
        background-image: none;

        &::after {
            background-image: none;
        }
    }
}

.requisitos {
    @include m.tablet {
        background-image: 
            linear-gradient(to right, transparent 50%, v.$purple_400 50%, v.$purple_400 100%),
            image-set(url("/build/img/design/requisitos.avif") 1x, url("/build/img/design/requisitos.webp") 1x, url("/build/img/design/requisitos.jpg") 1x);
        background-repeat: no-repeat;
        background-size: 100%, 50% 100%;
        background-position: left;

        &__grid {
            display: grid;
            grid-template-columns: 50% 50%;
        }

        &__contenido {
            grid-column: 2 / 3;
            padding: 2rem;
        }

        &__titulo {
            color: v.$white;
            font-size: 5.5rem;
        }
    }
}

.lista-requisitos {
    margin-top: 3rem;

    &__item {
        position: relative;
        margin-bottom: 3rem;

        &:last-child {
            margin-bottom: 0;
        }

        &::before {
            content: '';

            position: absolute;
            left: -18px;
            top: 7.5px;

            width: 0;
            height: 0;

            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            border-left: 10px solid v.$purple_200;
        }
        
        p {
            color: v.$black;
            font-weight: v.$light;
            margin: 0;
        }

        @include m.tablet {
            p {
                color: v.$white;
            }
        }
        
        span {
            font-weight: v.$bold;
        }
    }
}