header {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  padding: 0.75rem 2rem;
  width: 100%;
  height: 6.5rem;
  background-color: #760652;
}
@media (min-width: 768px) {
  header {
    flex-direction: column;
    padding: 0.75rem 0 0 0;
    height: 10.75rem;
  }
  header .contenedor-logo {
    display: flex;
    justify-content: center;
  }
}
@media (min-width: 1024px) {
  header {
    flex-direction: row;
    height: 6.5rem;
    padding: 0.75rem 5rem;
  }
}

.logo {
  height: 5rem;
}

.boton-menu {
  z-index: 2;
}
@media (min-width: 768px) {
  .boton-menu {
    display: none;
  }
}
.boton-menu__icon {
  position: absolute;
  right: 2rem;
  bottom: 2rem;
  color: #ffffff;
  font-size: 2.5rem;
}

.menu {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 3.5rem;
  background-color: rgba(78, 1, 43, 0.9);
  padding: 7rem 2rem;
  height: 100vh;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 150ms ease-in, visibility 150ms ease-in;
}
@media (min-width: 768px) {
  .menu {
    position: relative;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    background-color: #580531;
    height: 100%;
    padding: 0.75rem 0;
    opacity: 1;
    visibility: visible;
  }
}
@media (min-width: 1024px) {
  .menu {
    background-color: initial;
    width: min-content;
  }
}
.menu__enlace {
  all: unset;
  color: #FFFFFF;
  font-size: 1.6rem;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  position: relative;
  height: min-content;
  cursor: pointer;
  font-size: 1.4rem;
}
.menu__enlace::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: -5px;
  width: calc(100% + 10px);
  height: 1px;
  background-color: #FFFFFF;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 300ms ease-in-out;
}
.menu__enlace:hover::before {
  transform: scaleX(1);
}

.mobile-menu {
  opacity: 1;
  visibility: visible;
}

.hero {
  background-image: image-set(url("/build/img/design/hero.avif") 1x, url("/build/img/design/hero.webp") 1x, url("/build/img/design/hero.jpg") 1x);
  background-size: cover;
  background-position: center;
  position: relative;
  padding: 2rem;
  height: 35rem;
  margin-top: 6.5rem;
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0.65;
}
.hero .hero__contenido {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .hero {
    margin-top: 10.75rem;
  }
}
@media (min-width: 1024px) {
  .hero {
    margin-top: 6.5rem;
    height: 45rem;
  }
}
@media (min-width: 1200px) {
  .hero {
    height: 50rem;
  }
}
@media (min-width: 480px) {
  .hero__contenido {
    align-items: flex-start !important;
  }
}
.hero__titulo {
  margin: 0;
  border: none;
  text-transform: uppercase;
  color: #d62fc0;
}
.hero__msg {
  font-size: 1.5rem;
  font-weight: 200;
  color: #FFFFFF;
}
.hero__msg span {
  display: block;
  margin-bottom: 0.6rem;
  font-size: 2.2rem;
  font-weight: 500;
}
@media (min-width: 1024px) {
  .hero__msg {
    font-size: 1.8rem;
  }
  .hero__msg span {
    font-size: 2.8rem;
  }
}
@media (min-width: 1200px) {
  .hero__msg {
    font-size: 2rem;
  }
  .hero__msg span {
    font-size: 3.5rem;
  }
}
.hero__ubicacion {
  font-size: 1.6rem;
  color: #fca2e7;
}
.hero__ubicacion i {
  margin-right: 1rem;
}
.hero__msg, .hero__ubicacion {
  text-align: center;
}
@media (min-width: 480px) {
  .hero__msg, .hero__ubicacion {
    text-align: start;
  }
}

.nosotros__imagen {
