.contenedor-icono {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  text-align: center;
  padding: 2rem;
  color: #FFFFFF;
}
.contenedor-icono::before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 10rem;
  width: 10rem;
  clip-path: circle(50%);
  background: linear-gradient(90deg, #d62fc0 50%, #de5acd 50%);
