.accordion {
  margin-top: 2rem;
}
.accordion__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.accordion__item:nth-child(n+2) .accordion__fondo-titulo {
  border-top: none;
}
.accordion__fondo-titulo {
  background-color: #97105C;
  border: 3px solid #760652;
  transition: background-color 200ms;
}
.accordion__fondo-titulo:hover {
  background-color: #bc1472;
}
.accordion__contenedor-titulo {
  display: flex;
  align-items: center;
  gap: 5rem;
  padding: 1rem 0;
  min-height: 6rem;
}
.accordion__contenedor-titulo:hover {
  cursor: pointer;
}
.accordion__titulo, .accordion__icono {
  color: #FFFFFF;
}
.accordion__titulo {
  flex: 98%;
  text-align: left;
}
.accordion__icono {
  flex: 2%;
  transform: rotate(180deg);
  transition: transform 250ms ease-in;
}
.accordion__contenido {
  max-height: 0;
  padding: 0;
  margin: 0;
  overflow: hidden;
  transition: max-height 400ms, margin 400ms, padding 400ms, box-shadow 400ms;
}

.activo .accordion__fondo-titulo {
  background-color: #bc1472;
}
.activo .accordion__contenido {
  margin: 1.5rem 0;
  padding: 0 1rem;
}
